@font-face {
  font-family: 'owfont';
  src: url('../../../../fonts/owfont-regular.eot?v=1.0.0');
  src: url('../../../../fonts/owfont-regular.eot?#iefix&v=1.0.0')
      format('embedded-opentype'),
    url('../../../../fonts/owfont-regular.woff') format('woff'),
    url('../../../../fonts/owfont-regular.ttf') format('truetype'),
    url('../../../../fonts/owfont-regular.svg#owf-regular') format('svg');
  font-weight: normal;
  font-style: normal;
}

i:hover {
  animation: shake 1.5s;
}

.owf {
  display: inline-block;
  font: normal normal normal 14px/1 owfont;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0, 0);
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
}
